/* Установка единых стилей для HTML5 элементов */
article, aside, footer, header, nav, section {
    display: block;
  }
  
  /* Обеспечение единых стилей для списков */
  ol, ul {
    list-style: none;
  }
  
  /* Устранение различий в размерах шрифтов */
  body {
    font-size: 100%;
    line-height: 1;
  }