:root {
  background-color: rgb(255,255,255);
  display: grid;
  place-content: center;
  color-scheme: white;
}

.main-container {
  display: flex;

  flex-direction:column;
  place-content: center;
  counter-reset: example 0;
  padding-top: 50px;
  padding-bottom: 50px;
  top: 25%;     /* Вертикальное центрирование */
  width: 50vw;  /* Ширина контейнера равна ширине окна браузера */
  background-color: rgb(240, 240, 240);
}                                                                                                             


.initials {
  box-sizing: border-box;
  margin: auto;
}

.initials h1 h2 {
  font-family: "Open Sans", "Helvetica Neue", Arial, sans-serif;
  font-weight: lighter;
  justify-content: left;
}

h1 {
  padding: 20px;  
}